.cases{
    background: linear-gradient(471deg, #380f68,  #0c2f26,#0e2177,  #071813);
    background-size: 400% 400%!important;
    border-color: #2e2640;
    border-style: solid;
    border-width: 1px 1px 1px 1px;
   flex-direction: row;
    display: flex;
    flex-wrap: wrap;
    user-select: none;
    width: 100%;
    transition: background 0.4s ease-in-out, color 0.6s ease-in-out;
    -webkit-animation: keyboardActive 5s ease infinite;
    -moz-animation: keyboardActive 5s ease infinite;
    animation: keyboardActive 5s ease infinite; 
}

.keyboardActive::before{
-webkit-animation: keyboardActive 5s ease infinite;
-moz-animation: keyboardActive 5s ease infinite;
animation: keyboardActive 5s ease infinite;  
transition: background 0.4s ease-in-out, color 0.6s ease-in-out;

}
@-webkit-keyframes keyboardActive {
    0%{background-position:10% 0%}
    50%{background-position:91% 100%}
    100%{background-position:10% 0%}
}
@-moz-keyframes keyboardActive {
    0%{background-position:10% 0%}
    50%{background-position:91% 100%}
    100%{background-position:10% 0%}
}
@keyframes keyboardActive { 
    0%{background-position:10% 0%}
    50%{background-position:91% 100%}
    100%{background-position:10% 0%}
}

